@import url(https://fonts.googleapis.com/css?family=Dosis);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Dosis);
@import url(https://fonts.googleapis.com/css?family=Syncopate);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wallpaper {
  max-width: 100%;
  height: auto;
  position: relative;
}

.blog-title {
  padding-top:3%;
  text-align: center;
  font-size: 32px;
  font-family: 'Montserrat';
}

.blog-content {
  text-align: left;
  margin-left: 10%;
  margin-right: 10%;
  font-size: 20px;
  line-height: 1.5;
  font-family: 'Dosis',serif;
}
.wallpaper {
  max-width: 100%;
  height: auto;
  position: relative;
}

.profilePic {
  max-width: 12%;
  max-height: auto;
  border-radius: 50%;
  border: 3px solid #fff;
  position:absolute;
  top:50%;
  left:9%;
}

.name {
  padding-top: 8%;
  margin-bottom: 0;
  font-family: 'Syncopate';
}

.name b{
  font-size: 32px;
}

.intro {
  text-align: left;
  margin-left: 10%;
  margin-right: 20%;
  font-size: 20px;
  line-height: 1.5;
  font-family: 'Dosis',serif;
}

.texts {
  text-align: left;
  width: 100%;
  font-size: 20px;
  line-height: 1.5;
  font-family: 'Dosis',serif;
}

*,*::before,*::after{
    box-sizing: border-box;
}
.nameAndContacts {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 100%;
  max-width: calc(100% - 20%);
  padding-top: 40px;
}
.name {
  margin: 0;
  font-size: 20px;
}
.icons-box{
    display: flex;
    align-items: center;
}
.seperator{
    height: 20px;
    min-height: 20px;
    width: 1px;
    min-width: 1px;
    background: #ccc;
    margin: 0 15px;
}
.icons a{
    display: block;
}
.icons img {
    min-height: 30px;
    min-width: 30px;
    max-height: 30px;
    max-width: 30px;
    display: block;
    margin: 0;
}

.body{
  display:flex;
  margin-left: 10%;
  flex-direction: column;
}

.blogEntry{
  display:flex;
  flex-direction: row;
  justify-content:flex-start;
}

.date {
  text-align: left;
  width: 100px;
  font-size: 20px;
  line-height: 1.5;
  font-family: 'Dosis',serif;
}

.title {
  text-align: left;
  width: 100%;
  font-size: 20px;
  line-height: 1.5;
  font-family: 'Dosis',serif;
}

.category div{
  float: left;
}

.categoryIcons{
  min-height: 70px;
  min-width: 70px;
  max-height: 70px;
  max-width: 70px;
  padding: 15px;
}

.content-codingProject p{
  padding-right:20%;
  height: 225px;
  width: 100%;
  overflow-y: scroll;
  background-color: aliceblue;
}

.content{
  font-size: 20px;
  font-family: 'Dosis',serif;
}

.footer{
  margin-right: 10%;
  font-size: 16px;
  color:gray;
  text-align: right;
  font-family: 'Dosis',serif;
  padding-top: 150px;
}

body{
  background-color: ghostwhite;
}

a:hover,a:visited,a:link,a:active { color:#000; }
